.SattingsWrap {
  display: flex;
  width: 144px;
  /* height: 48px; */
  justify-content: space-between;
}

.ItemsWrap {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 48px;
  width: 48px;
  border-radius: 50%;
  cursor: pointer;
}

.ItemsWrap:hover {
  background-color: lightgray;
  .Icon {
    color: black;
  }
}

.Icon {
  color: gray;
}
